.header {
    .container_small {
        padding: 0 0 0 24px;
        max-width: 1021px;
        @include md {
            padding: 0 60px 0 36px; } }
    &__content {
        position: relative;
        display: block;
        height: 146px;
        padding-top: 62px;
        @include clr;
        @include md {
            padding-top: 57px; }
        .menu_main {
            display: inline-block;
            position: absolute;
            @include vertical; }
        .logo {
            margin-right: 125px;
            @include md {
                margin-right: 64px; } } } }


.nav_right {
    display: block;
    float: right;
    @include clr;
    .account {
        display: inline-block;
        margin-right: 6px;
        padding: 12px 28px;
        color: $main_color;
        font-size: em(16);
        font-family: $main_font;
        font-weight: 600;
        background-color: #fff;
        border-radius: 17px;
        @include md {
            font-size: 14px;
            padding: 9px 19px; } } }


.language {
    display: flex;
    flex-direction: column;
    float: right;
    padding: 13px 24px;
    color: #ffffff;
    border-radius: 20px;
    @include md {
        padding: 9px 13px; }

    &_hidden {
        display: none; }
    &:hover {
        color: $main_color;
        background-color: #fff;
        .language_hidden {
            display: block; } }
    a {
        margin-bottom: 17px;
        &:last-child {
            margin-bottom: 0; } }
    a:hover {
        text-decoration: underline; } }
